.nav-ul li{
  display: inline-block;
  padding: 10px;
}
.nav-ul {
  padding: 5px;
  margin: 0;
  background-color: skyblue;
}
.nav-ul li a{
  color: white;
  padding: 5px;
  text-decoration: none;
}
.footer{
  position: absolute;
  bottom: 0;
  text-align: center;
  width: 100%;
  background-color: skyblue;
}
.inputBox{
  display: block;
  margin: 25px;
  padding: 7px;
  width: 300px;
  border: 1px solid skyblue;

}
.appbutton{
  margin: 25px;
  padding: 10px;
  width: 150px;
  background-color: skyblue;
  border: 1px solid;
  cursor: pointer;
}
.register {
  margin-left: 30%;
}
.login{
  margin-left: 30%;
  margin-top: 100px;
}
.nav-right{
  text-align: right;
}
.product{
  margin-left: 30%;
}
.product-list{
  text-align: center;
  margin-top: 50px;

}
.product-list ul li {
  display: inline-block;
  border: 1px solid skyblue;
  padding: 5px;
  width: 150px;

}
.product-list ul {
  margin: 0;
}
.search-product-list{
  width:400px;
  margin:20px;
  height: 40px;
  border: 1px solid skyblue;
  padding-left :20px;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
